@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
*{
    --mainColor: 242, 197, 124;
    --secondaryColor: 221, 174, 126;
    --thirdColor: 127, 182, 133;
    --red: 239, 111, 108;
    -webkit-user-select: none;
    -ms-user-select: none;
        user-select: none;
    -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;
}
.blur {
    background: rgba( 255, 255, 255, 0.2 );

backdrop-filter: blur( 10px );
-webkit-backdrop-filter: blur( 10px );

}
html {
    height: -webkit-fill-available;
}
body {
    padding: 0;
    margin: 0;
    min-height: 100vh;
    min-height: -webkit-fill-available;
}

.Oeuf {
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    top: 0;
    transition-property: top, opacity;
    transition-duration: 1s,2s;
    transform-style: ease-out;
    transition-delay: 0s,1s;
    opacity: 1;
}
.Oeufanim {
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    display: -webkit-flex;
    align-items: center;
    justify-content: center;

}

.panier {
    position: absolute;
    bottom: -5%;
    width: 200%;

}

.Oeufapp {
    width: 150px;
    height: 220px;
    position: relative;
    top:60%;
    left:50%;
    transform: translate(-50%, -50%);
    display: flex;
    display: -webkit-flex;
    align-items: center;
    justify-content: center;
    margin:0;
}

.up {
    top: -30%;
    opacity:0;
}

.down {
    opacity:0;
    top: 10%;
}

#tapIcon {
    position: absolute;
    top: 55%;
    left: 65%;
    transform: translate(-50%,-50%);
    color: blue;
    width: 65%;
    pointer-events: none;
}

.indicationOeuf {
    position: absolute;
    top: -40%;
    left: 10%;
    font-family: 'Indie Flower', cursive;
    font-size: 200%;
    font-weight: bold;
    white-space:nowrap;
}

#arrow {
    position: absolute;
    top: -30%;
    left: -70%;
    width: 150%;
    height: auto;
    transform: rotate(55deg);

}

@media (min-width: 640px) and (min-height: 800px) {
    .Oeufapp {
        width: 300px;
        height: 440px;
    }
}
.Activity {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 0 0 100%;
    width: 50%;
    height: 0;
    padding-bottom: 60%;
    top: 40%;
    position: absolute;
    background-color: whitesmoke;
    border-radius: 15%; 
box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0,0,0,0);
transition-property: width,padding, top, font-size;
transition-duration: 1s,1s;
transition-timing-function: ease-in-out,ease-in-out;
transition-delay: 1.2s,1.2s;
    
}



.wrapper {
    height: 0;
    overflow: hidden;
    padding-top: 95%;
    box-sizing: border-box;
    position: relative;
    border-radius:15%;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    width: 100%;
}

.imageActivity {
    object-fit: cover;
    width: 100%;
  vertical-align: top;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
}

.activityName {
    display: block;
    width: 95%;
    font-size: 40%;
    text-align: center;
    padding-bottom: 3px;
    font-family:'Permanent Marker', cursive;
    color:#46b3e6;
    overflow-wrap: break-word;
}



.reload {
    position: absolute;
    left: 98%;
    top: 0%;
    transform: translate(-50%,-50%);
    width: 23%;
}

.popOut {
    width: 120%;
    padding-bottom: 144%;
    font-size: 240%;
    top: -30%;

}

@media screen and (min-width: 640px) and (min-height: 800px) {
    .activityName {
        font-size: 80%;
    }
}

.main {
    position:relative;
    padding:0;
    margin:0;

    top:0;
    left:0;

    width: 100%;
    height: 100%;

    background-position: center;
    background-size: cover;

}
.goToAdd {
    position: absolute;
    left:5px;
    top: 5px;
    width: 50px;
}



@media (min-width: 800px)  {
    .addActivity {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    
}
}


.fond{
    position:absolute;
    padding:0;
    margin:0;

    top:0;
    left:0;

    width: 100%;
    height: 100%;

    background-position: center;
    background-size: cover;

}

.goBack {
    position: absolute;
    left:5px;
    top: 5px;
    width: 50px;
}

.plus, .previewImg{
    border: 2px solid #ffffff;
	background-color: rgba(248, 248, 248, 0.15);
	font-size: 16px;
	height: 2.5em;
	width: 2.5em;
	border-radius: 30px;
	position: relative;
    font-size: 55px;
    object-fit: cover;
}

.plus::after{
    content: "";
    display: block;
    background-color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 0.2em;
    width: 1em;
}
.plus::before {
    content: "";
    display: block;
    background-color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 1em;
    width: 0.2em;
}
.formActivity {
    position: relative;
    top: 50%;
    left: 50%;
    height: 37%;
    min-height: 250px;
    transform: translate(-50%,-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.input {
    border: 2px solid white;
    border-radius: 8px;
    text-align:center;
    height: 28px;
    width: 100%;
    -webkit-user-select: text;
        -ms-user-select: text;
            user-select: text;
    background-color: transparent;
}
.inputField {
    width: 50%;
    position: relative;
}

.envoi {
    
    width: 25px;
    margin-top: 1px;
    position: absolute;
    right: -35px;

}
.envoi::before {
    content: '';
    display: block;
    position: relative;
    width: 50%;
    height: 50%;
    background-color: white;
}

.envoi:active {
    filter: brightness(80%);
}

@media (min-width: 800px)  {
    .formActivity {
        max-width: 650px;
        width: 60%;
        height: 260px;
    position: static;
    transform: none;
    
    }
}
ul {
    list-style-type: none;
    
}



@media (min-width:320px)  { 

    .listActivity {

        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        position: absolute;
        top: 100%;
        transition: top;
        transition-duration: 0.75s;
        transition-timing-function:ease;
        background-color: white;
        background-color: transparent;
        overflow: scroll;
    }

    .showHide{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 5.1%;
        position: absolute;
        top: 95%;
        transition: transform, top, background-color;
        transition-duration: 0.75s,0.75s,0s;
        transition-delay: 0s,0s,0s;
        background-color: transparent;
        
    }

    .turn {
        transform: rotate(180deg);
        top: 0;
        background-color: white;
        transition: transform, top, background-color;
        transition-duration: 0.75s,0.75s,0s;
        transition-delay: 0s,0s,0.75s;
    }


    .show{
        top:5%;
        background-color: white;
    }

    .showHide:first-child {
        color: #0d6efd;
        
        
    }
    .allItems {
        background-color: white;
        padding: 0;
        width: 80%;
    }
}

@media (min-width: 800px) {
    .showHide{
        display: none;
        background-color: transparent;
    }

    .listActivity{
        width: 30%;
        position:relative;
        background-color: rgba(255, 255, 255,0.5);
        top: 0;
        height: 80%;
        border-radius: 10px;
    }
    .allItems {
        padding: 0;
        width: 90%;
        margin-top: 5%;
    }
        
}
