}

.loadZone {
    position: absolute;
    background-color: white;
    z-index: 2;
    width: 100%;
    height: 100%;
    top:0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loadChiken {
    width: 400px;
    height: 400px;
}
